@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
.container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    margin-top: 2em;
}
.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100vw;
}
.box {
    width: 600px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
p {
    font-size: 1.25em;
    color: #f5dfbb;
    word-spacing: 10px;
    padding-top: 0.75em;
}
p > span {
    color: #f5f3f4;
}
.symbol {
    padding-bottom: 0.5em;
    color: #f5dfbb;
}
.report {
    color: #ba181b;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Lato", sans-serif;
    background-color: #0b090a;
}
.app {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;
    width: 100vw;
}
.dataInput {
    color: white;
    outline: none;
    border-radius: 5px;
    border: none;
    border: 1px #f5f3f4 solid;
    width: 20em;
    padding: 0.5em 1.5em;
    font-size: 1.5em;
    margin-top: 2em;
}
.dataInput::-webkit-input-placeholder {
    text-align: center;
}
.dataInput:-ms-input-placeholder {
    text-align: center;
}
.dataInput::placeholder {
    text-align: center;
}
.lineChart {
    width: 80vw;
}
.displayChart {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

